import { Composition } from "remotion";
import { MyVideo, myVideoSchema } from "./MyVideo";
import './index.css';
import scenario from '../public/scenario.json'; // public/scenario.json 

export const RemotionRoot = () => {
  const SCENE_DURATION = 100; // 

  return (
    <>
      <Composition
        id="MyVideo"
        component={MyVideo}
        durationInFrames={scenario.length * SCENE_DURATION} // 
        fps={30}
        width={1920}
        height={1080}
        schema={myVideoSchema}
        defaultProps={{
          scenario: scenario, // Props
        }}
      />
    </>
  );
};